export default function Header(props) {

    const { leftBtn, leftFunc, title, rightCancel, rightXBtn, rightFunc } = props;

    useEffect(() => {
        
    }, []);

    return (
        <div className="header">
            <div>
                {
                    leftBtn ? 
                    <img src={require("../../assets/images/icons/back_btn.svg").default} style={{ padding: 5 }} onClick={() => { leftFunc() }}/>
                    :
                    <div style={{ width: 52, height: 52 }}/>

                }
            </div>
            <p className='center_text'>
                {title}
            </p>
            <div>
                {
                    rightCancel
                    && 
                    <button className='cancel_text' onClick={() => { rightFunc() }} style={{ padding: 10 }}>취소</button>
                }
                {
                    rightXBtn
                    && 
                    <img 
                        src={require("../../assets/images/icons/x_btn.svg").default} 
                        onClick={() => { rightFunc() }} 
                        style={{ padding: 5 }}
                    />
                }
            </div>
        </div>
    )    
}