import React, { useEffect, useState, useRef, forwardRef } from "react";
import { useDispatch, useSelector } from 'react-redux';
import moment from 'moment';

import routes from "../../libs/routes";
// import consts from '../../libs/consts';

import { logIn, logOut } from '../../redux/usersSlice';
import { open, close } from '../../redux/popupSlice';
import { loadingStart, loadingEnd } from '../../redux/loadingSlice';

export default function Popup01(props) {

    const { text, width, button, buttonCancel, func, setFunc, noneLoading } = props;

    const dispatch = useDispatch();

    const sucFunc = () => {

        if(func === 'setFunc') setFunc();

        dispatch(close());

    }

    return (
        <>
            <div className="popup_default01" style={{ width: width }}>
                <div className="popup_text_box">
                    <p>{text}</p>
                </div>
                <div className="popup_btn_box">
                    {buttonCancel &&
                        <button className="button_cancel" onClick={() => dispatch(close())}>
                            {buttonCancel}
                        </button>
                    }               
                    {button && 
                        <button className="button_success" onClick={() => sucFunc()}>
                            {button}
                        </button>
                    } 
                </div>
            </div>
        </>
    );
}
