import React, { useEffect, useState, useRef, useLayoutEffect } from "react";
import { useDispatch, useSelector } from 'react-redux';
import axios from "axios";
import { useNavigate, useLocation } from 'react-router';
import moment from 'moment';

// import * as APIS from "../../utils/service";

import routes from '../../libs/routes';
// import consts from '../../libs/consts';
import { numFormat, patternNickFunc, regEmail, regPassword, regNameChk, regPhoneChk, patternSpcInstar, regPhone } from "../../utils/utils";

import { logIn, logOut, gpsUpdate, configData, setData, aroundData, menuClose, mainReset, searchClose, pickerClose } from '../../redux/usersSlice';
import { open, close } from '../../redux/popupSlice';
// import { openSub, closeSub } from '../../redux/subPopupSlice';
import { loadingStart, loadingEnd } from '../../redux/loadingSlice';

import Header from "../parts/Header";
import Popup01 from '../popup/Popup01';
import TermsPopup from '../popup/TermsPopup';

/* component */
import Input from "../../components/Input";
import InputFile from "../../components/InputFile";
import InputProFile from "../../components/InputProFile";
import Button01 from "../../components/Button01";
import Button02 from "../../components/Button02";
import Check01 from "../../components/Check01";
import FooterBtn from "../../components/FooterBtn";



export default function RegisterInput(props) {
    
    const dispatch = useDispatch();
    const navigate = useNavigate();
    const location = useLocation();
    const { state } = location;

    // if(!state?.user_type) navigate(routes.register);

    const [pfImg, setpfImg] = useState(''); // 이미지 base64파일
    const [pfImgPre, setpfImgPre] = useState(''); // 이미지 url
    const [pass, setPass] = useState('');
    const [passChk, setPassChk] = useState('');
    const [name, setName] = useState('');
    const [phone, setPhone] = useState('');

    const [err01, setErr01] = useState('');
    const [err02, setErr02] = useState('');
    const [err03, setErr03] = useState('');
    const [err04, setErr04] = useState('');

    const [chk01, setChk01] = useState(false);
    const [chk02, setChk02] = useState(false);
    const [chk03, setChk03] = useState(false);

    const [sucChk, setSucChk] = useState(false);

    useEffect(() => {
        
    }, [pfImg]);
    
    const goLoginFunc = () => {
        navigate(routes.login);
    }

    const passChkFunc = (type) => {
        
        if(!regPassword(pass)) {
            setErr01('비밀번호는 8~12자리 영문,숫자,특수문자 혼합');
            return;
        }
        setErr01('');
        
        if(pass !== passChk) {
            setErr02('비밀번호가 일치하지 않습니다.');
            return
        }
        setErr02('');

        setChk01(true);
    }

    const nameChkFunc = () => {
        if(!regNameChk(name)) {
            setErr03('2~10자 한글,영문으로 입력하세요.');
            return;
        }
        setErr03('');

        setChk02(true);
    }

    const phoneChkFunc = () => {
        if(!regPhoneChk(phone)) {
            setErr04('휴대폰 형식이 올바르지 않습니다.');
            return;
        }
        setErr04('');

        setChk03(true);
    }

    const naviFunc = () => {
        if(chk01 && chk02 && chk03) {
            // 여기에서 회원가입 데이터 전송
            if(true) {
                setSucChk(true);
            } 
        } 
    }
    
    return (
        <>
            {!sucChk ? 
                <>
                    <Header 
                        leftBtn={true}
                        leftFunc={() => {

                        }}
                        title={'계정 만들기'}
                        rightXBtn={true}
                        rightFunc={() => {
                            goLoginFunc();
                        }}
                    />
                    <div className="flex_container">
                        <div className="register_box_title" style={{ marginBottom: 40 }}>
                            <p>계정에 사용할</p>
                            <p>정보를 등록해 주세요.</p>
                        </div>
                        <div className="register_avatar_col" style={{ marginBottom: 40 }}>
                            <p className="register_info_title">이메일 ID</p>
                            <p className="register_info_email">{state?.email}</p>

                            <InputProFile 
                                value={pfImg}
                                setValue={setpfImg}
                                imgvalue={pfImgPre}
                                setImgValue={setpfImgPre}
                            />
                            
                        </div>

                        <div>
                            <div style={{ marginBottom: 10 }}>
                                <Input 
                                    type={'password'}
                                    label={'비밀번호'}
                                    value={pass}
                                    setValue={setPass}
                                    error={err01}
                                    onBlur={() => {
                                        passChkFunc(1)
                                    }}
                                />
                            </div>
                            <div style={{ marginBottom: 20 }}>
                                <Input 
                                    type={'password'}
                                    value={passChk}
                                    setValue={setPassChk}
                                    error={err02}
                                    onBlur={() => {
                                        passChkFunc(2)
                                    }}
                                />
                            </div>

                            <div style={{ marginBottom: 20 }}>
                                <Input 
                                    label={'이름'}
                                    value={name}
                                    setValue={setName}
                                    error={err03}
                                    onBlur={() => {
                                        nameChkFunc()
                                    }}
                                />
                            </div>
                            <div>
                                <Input 
                                    label={'휴대폰번호'}
                                    value={phone}
                                    setValue={setPhone}
                                    error={err04}
                                    onBlur={() => {
                                        phoneChkFunc()
                                    }}
                                />
                            </div>
                        </div>
                        
                        <FooterBtn
                            greyChk={!chk01 || !chk02 || !chk03 && true}
                            setFunc={naviFunc}
                        />
                        
                    </div>
                </>
                :
                <>
                    <div className="suc_container">
                        <p className="suc_title">PreNote</p>
                        <img src={require("../../assets/images/icons/avatar.svg").default} style={{ width: 64, marginBottom: 40 }}/>
                        <p className="suc_name">{name} 님</p>
                        <p className="suc_title02">회원가입을 환영합니다.</p>
                        <Button01 
                            label={'시작하기'}
                            setFunc={() => {
                                alert('메인 이동');
                            }}
                        />
                    </div>
                </>
            }
        </>
    );
}
